import React from 'react'
import './Portfolio.css'
import { AiFillEye, AiOutlineShareAlt } from "react-icons/ai";


const Portfolio = ({imgUrl, title}) => {
  return (
    <div className='ace__portfolio'>
      <img src={imgUrl} alt="services"/>
      <p>{title}</p>

        <AiFillEye className='icons' size={24} padding={10}/>
        <a href='https://google.com'><AiOutlineShareAlt className='icons' size={24}/></a>
      
      

    </div>
  )
}

export default Portfolio