import React from 'react'
import './Work.css'
import {Heading} from '../../components'
import {Portfolio} from '../../components'
import {Graphics1, ArrowLeft, ArrowRight} from '../../assets'
import 'antd/dist/antd.css';
import { Carousel } from 'antd';

var slide = {
  dots: false,
  infinite: true,
  autoplay: true,
  variableWidth:true,
  speed: 500,
  swipeToSlide:true,
  slidesToShow: 1,
  slidesToScroll: 1,
  focusOnSelect:true,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 1,
      }
    },
    {
      breakpoint: 768,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 1,
        initialSlide: 2
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};

const Work = () => {

  return (
    <div className="ace__work section__padding reveal" id='work'>
        < Heading title="My Work" sub="Recent Projects"/>

        <div className='ace__work-arrow'>
          <img src={ArrowLeft} alt="arrow left"/>
          <img src={ArrowRight} alt ="arrow right"/>

        </div>

        <div className="ace__work-portfolio">
          <Carousel {...slide} className='ace__work-portfolio__list'>
            <Portfolio imgUrl={Graphics1} title='Web Design'/>
            <Portfolio imgUrl={Graphics1} title='Web Design'/>
            <Portfolio imgUrl={Graphics1} title='Web Design'/>
            <Portfolio imgUrl={Graphics1} title='Web Design'/>
            <Portfolio imgUrl={Graphics1} title='Web Design'/>
          </Carousel>


        </div>

    </div>
  )
}

export default Work